export function getDayTextByDate(date) {
    return getDayText(new Date(date).getDay());
}

export function getDayText(dayIndex)
{
    switch(dayIndex)
    {
        case 0:
            return 'Sunday';
        case 1:
            return 'Monday';
        case 2:
            return 'Tuesday';
        case 3:
            return 'Wednesday';
        case 4:
            return 'Thursday';
        case 5:
            return 'Friday';
        case 6:
            return 'Saturday';
        default:
            return 'Invalid Date';
    }
}
